<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 工序编码 -->
            <a-form-model-item
              label="工序编码"
              prop="operationCode"
              ref="operationCode"
            >
              <a-input
                v-model="entity.operationCode"
                placeholder="请输入工序编码"
                name="operationCode"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 工序名称 -->
            <a-form-model-item
              label="工序名称"
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder="请输入工序名称"
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 工序类型 -->
            <a-form-model-item
              label="工序类型"
              prop="operationType"
              ref="operationType"
            >
              <a-select
                v-model="entity.operationType"
                placeholder="请选择工序类型"
                name="operationType"
                @change="handleOperationTypeChange"
              >
                <a-select-option value="加工工序">加工工序</a-select-option>
                <a-select-option value="装配工序">装配工序</a-select-option>
                <a-select-option value="检验工序">检验工序</a-select-option>
                <a-select-option value="热处理工序">热处理工序</a-select-option>
                <a-select-option value="表面处理工序">表面处理工序</a-select-option>
                <a-select-option value="包装工序">包装工序</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 执行部门 -->
            <a-form-model-item
              label="执行部门"
              prop="department"
              ref="department"
            >
              <a-select
                v-model="entity.department"
                placeholder="请选择执行部门"
                name="department"
              >
                <a-select-option value="机加部">机加部</a-select-option>
                <a-select-option value="装配部">装配部</a-select-option>
                <a-select-option value="质检部">质检部</a-select-option>
                <a-select-option value="热处理部">热处理部</a-select-option>
                <a-select-option value="表面处理部">表面处理部</a-select-option>
                <a-select-option value="包装部">包装部</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 标准工时 -->
            <a-form-model-item
              label="标准工时"
              prop="standardTime"
              ref="standardTime"
            >
              <a-input-number
                v-model="entity.standardTime"
                :min="0"
                :precision="2"
                style="width: 100%"
                placeholder="请输入标准工时"
                name="standardTime"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 时间单位 -->
            <a-form-model-item
              label="时间单位"
              prop="standardUnit"
              ref="standardUnit"
            >
              <a-select
                v-model="entity.standardUnit"
                placeholder="请选择时间单位"
                name="standardUnit"
              >
                <a-select-option value="秒">秒</a-select-option>
                <a-select-option value="分钟">分钟</a-select-option>
                <a-select-option value="小时">小时</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-radio-group v-model="entity.status" name="status">
                <a-radio value="启用">启用</a-radio>
                <a-radio value="禁用">禁用</a-radio>
                <a-radio value="开发中">开发中</a-radio>
              </a-radio-group>
            </a-form-model-item>
          </a-col>
          <a-col :sm="24">
            <!-- 工序描述 -->
            <a-form-model-item 
              label="工序描述" 
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入工序描述"
                :rows="3"
                :max-length="500"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <!-- 工序参数 -->
      <g-collapse headerName="工序参数">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-param-btn" type="primary" icon="plus" @click="addParameter">添加参数</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="parameterColumns" 
                :dataSource="entity.parameters" 
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <template slot="paramName" slot-scope="text, record, index">
                  <a-input v-model="entity.parameters[index].paramName" placeholder="请输入参数名称" />
                </template>
                <template slot="paramValue" slot-scope="text, record, index">
                  <a-input v-model="entity.parameters[index].paramValue" placeholder="请输入参数值" />
                </template>
                <template slot="paramUnit" slot-scope="text, record, index">
                  <a-input v-model="entity.parameters[index].paramUnit" placeholder="请输入参数单位" />
                </template>
                <template slot="controlRange" slot-scope="text, record, index">
                  <a-input v-model="entity.parameters[index].controlRange" placeholder="请输入控制范围" />
                </template>
                <template slot="required" slot-scope="text, record, index">
                  <a-switch v-model="entity.parameters[index].required" />
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button 
                    type="danger" 
                    icon="delete" 
                    size="small" 
                    @click="() => removeParameter(index)"
                  />
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 设备要求 -->
      <g-collapse headerName="设备要求">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-equipment-btn" type="primary" icon="plus" @click="addEquipment">添加设备</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="equipmentColumns" 
                :dataSource="entity.equipments" 
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <template slot="equipmentType" slot-scope="text, record, index">
                  <a-select
                    v-model="entity.equipments[index].equipmentType"
                    placeholder="请选择设备类型"
                    style="width: 100%"
                  >
                    <a-select-option value="车床">车床</a-select-option>
                    <a-select-option value="铣床">铣床</a-select-option>
                    <a-select-option value="钻床">钻床</a-select-option>
                    <a-select-option value="磨床">磨床</a-select-option>
                    <a-select-option value="热处理炉">热处理炉</a-select-option>
                    <a-select-option value="喷涂设备">喷涂设备</a-select-option>
                    <a-select-option value="检测设备">检测设备</a-select-option>
                    <a-select-option value="装配工具">装配工具</a-select-option>
                    <a-select-option value="包装设备">包装设备</a-select-option>
                  </a-select>
                </template>
                <template slot="equipmentModel" slot-scope="text, record, index">
                  <a-input v-model="entity.equipments[index].equipmentModel" placeholder="请输入设备型号" />
                </template>
                <template slot="quantity" slot-scope="text, record, index">
                  <a-input-number 
                    v-model="entity.equipments[index].quantity" 
                    :min="1"
                    style="width: 100%"
                    placeholder="请输入数量" 
                  />
                </template>
                <template slot="mandatory" slot-scope="text, record, index">
                  <a-switch v-model="entity.equipments[index].mandatory" />
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button 
                    type="danger" 
                    icon="delete" 
                    size="small" 
                    @click="() => removeEquipment(index)"
                  />
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 元数据信息 -->
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditOperation",
  mixins: [GNdoPageEditBase],
  
  data() {
    return {
      loading: false,
      entity: {
        id: undefined,
        operationCode: "",
        name: "",
        operationType: "",
        department: "",
        standardTime: 0,
        standardUnit: "分钟",
        description: "",
        parameters: [],
        equipments: [],
        status: "启用",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      },
      rules: {
        operationCode: [
          { required: true, message: "请输入工序编码", trigger: "change" },
          { pattern: /^[A-Z]{2}\d{3}/, message: "工序编码格式不正确", trigger: "change" }
        ],
        name: [
          { required: true, message: "请输入工序名称", trigger: "change" },
          { pattern: /^[^\s]+(\s+[^\s]+)*$/, message: "工序名称不能包含前后空格", trigger: "change" }
        ],
        operationType: [
          { required: true, message: "请选择工序类型", trigger: "change" }
        ],
        department: [
          { required: true, message: "请选择执行部门", trigger: "change" }
        ],
        standardTime: [
          { required: true, message: "请输入标准工时", trigger: "change" },
          { type: 'number', min: 0, message: "标准工时必须大于等于0", trigger: "change" }
        ],
        standardUnit: [
          { required: true, message: "请选择时间单位", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择状态", trigger: "change" }
        ]
      },
      parameterColumns: [
        {
          title: "参数名称",
          dataIndex: "paramName",
          key: "paramName",
          width: "20%",
          scopedSlots: { customRender: "paramName" }
        },
        {
          title: "参数值",
          dataIndex: "paramValue",
          key: "paramValue",
          width: "20%",
          scopedSlots: { customRender: "paramValue" }
        },
        {
          title: "参数单位",
          dataIndex: "paramUnit",
          key: "paramUnit",
          width: "15%",
          scopedSlots: { customRender: "paramUnit" }
        },
        {
          title: "控制范围",
          dataIndex: "controlRange",
          key: "controlRange",
          width: "20%",
          scopedSlots: { customRender: "controlRange" }
        },
        {
          title: "必填",
          dataIndex: "required",
          key: "required",
          width: "10%",
          scopedSlots: { customRender: "required" }
        },
        {
          title: "操作",
          key: "action",
          width: "10%",
          scopedSlots: { customRender: "action" }
        }
      ],
      equipmentColumns: [
        {
          title: "设备类型",
          dataIndex: "equipmentType",
          key: "equipmentType",
          width: "25%",
          scopedSlots: { customRender: "equipmentType" }
        },
        {
          title: "设备型号",
          dataIndex: "equipmentModel",
          key: "equipmentModel",
          width: "30%",
          scopedSlots: { customRender: "equipmentModel" }
        },
        {
          title: "数量",
          dataIndex: "quantity",
          key: "quantity",
          width: "15%",
          scopedSlots: { customRender: "quantity" }
        },
        {
          title: "必需",
          dataIndex: "mandatory",
          key: "mandatory",
          width: "15%",
          scopedSlots: { customRender: "mandatory" }
        },
        {
          title: "操作",
          key: "action",
          width: "15%",
          scopedSlots: { customRender: "action" }
        }
      ]
    };
  },
  
  methods: {
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取工序数据
        setTimeout(() => {
          this.initFormData();
          this.loading = false;
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      this.entity = {
        id: undefined,
        operationCode: "",
        name: "",
        operationType: "",
        department: "",
        standardTime: 0,
        standardUnit: "分钟",
        description: "",
        parameters: [],
        equipments: [],
        status: "启用",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      };
    },
    
    initFormData() {
      if (this.ndoEditParams.row) {
        const row = this.ndoEditParams.row;
        this.entity = {
          ...this.entity,
          ...row,
          // 确保数组被正确初始化
          parameters: row.parameters || [],
          equipments: row.equipments || []
        };
      }
    },
    
    async save() {
      // 保存校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        // 模拟保存操作
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e) {
      console.log(e.target.value);
    },
    
    handleOperationTypeChange(value) {
      // 根据工序类型设置默认执行部门
      const typeMap = {
        "加工工序": "机加部",
        "装配工序": "装配部",
        "检验工序": "质检部",
        "热处理工序": "热处理部",
        "表面处理工序": "表面处理部",
        "包装工序": "包装部"
      };
      
      if (typeMap[value]) {
        this.entity.department = typeMap[value];
      }
    },
    
    // 添加工序参数
    addParameter() {
      this.entity.parameters.push({
        id: Date.now(), // 使用时间戳作为临时ID
        paramName: "",
        paramValue: "",
        paramUnit: "",
        controlRange: "",
        required: false
      });
    },
    
    // 删除工序参数
    removeParameter(index) {
      this.entity.parameters.splice(index, 1);
    },
    
    // 添加设备要求
    addEquipment() {
      this.entity.equipments.push({
        id: Date.now(), // 使用时间戳作为临时ID
        equipmentType: "",
        equipmentModel: "",
        quantity: 1,
        mandatory: true
      });
    },
    
    // 删除设备要求
    removeEquipment(index) {
      this.entity.equipments.splice(index, 1);
    }
  },
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.add-param-btn,
.add-equipment-btn {
  margin-bottom: 16px;
}
</style> 